console.log("Executando...");

const buttonMais = document.querySelector('.btn-success');
const buttonMenos = document.querySelector('.btn-danger');


buttonMais.addEventListener('click', ()=> soma(+1));
buttonMenos.addEventListener('click', ()=> soma(-1));

const output = document.querySelector('output');

function soma(valor) {
    output.innerHTML = +output.innerHTML + valor;
}
